@use "../../global" as g;

.p-service {
  margin-bottom: 100px;

  @include g.mq(md) {
    margin-bottom: 60px;
  }
}

.p-service__heading {
  position: relative;
  margin-top: 50px;
  margin-bottom: 58px;

  @include g.mq(md) {
    margin-top: 0;
    margin-bottom: 22px;
  }
}

.p-service__heading::after {
  content: "SERVICE";
  color: g.$color-gray;
  font-size: 168px;
  font-weight: bold;
  letter-spacing: 0.03em;
  line-height: 1;
  position: absolute;
    top: -33px;
    right: -19px;
  z-index: -1;

  @include g.mq(md) {
    font-size: 7rem;
    line-height: 1.4;
    top: -2px;
    right: -16px;
  }
}

.p-service__list {
  display: grid;
  column-gap: 30px;
  grid-template-columns: repeat(3, 1fr);

  @include g.mq(md) {
    row-gap: 20px;
    grid-template-columns: 1fr;
  }
}