@use "../global" as g;

.l-header {
  width: 100%;
  background-color: g.$color-white;
}

.l-header__inner {
  display: flex;
  justify-content: space-between;
  height: 90px;
  
  @include g.mq(md) {
    height: 60px;
  }
}

.l-header__logo {
  padding-top: 28px;
  padding-bottom: 29px;
  margin-left: 69px;

  @include g.mq(md) {
    padding-top: 20px;
    padding-bottom: 19px;
    margin-left: 20px;
  }
}

.l-header__globalNav {
  margin: 35px 70px 34px 0;

  @include g.mq(md) {
    margin: 0;
    visibility: hidden;
  }
}

.l-header__hamburger {
  display: none;

  @include g.mq(md) {
    display: block;
  }
}

.l-header__spNav {
  display: none;
}
.l-header__spNav.active {
  display: block;
  width: 200px;
  height: 100vh;
  background-color: g.$color-primary;
  position: fixed;
  top: 0;
  right: 0;
  padding-top: 96px;
  padding-left: 40px;
  z-index: 20;
}

.l-header__spNavItem {
  margin-bottom: 28px;
}

#l-header__spNav-mask {
  display: none;
}
#l-header__spNav-mask.open {
  display: block;
  position: fixed;
    top: 0;
  width: 100vw;
  height: 100vh;
  background-color: transparent;
  z-index: 10;
}