@use "../../global" as g;

.c-summary {
  width: 100%;
  font-size: 1.5rem;
  letter-spacing: 0.05em;
  line-height: 1;

  @include g.mq(md) {
    font-size: 1.4rem;
    letter-spacing: 0;
  }
}

.c-summary th, td {
  padding: 31.5px 30px;
  border-bottom: 1px solid #ddd;

  @include g.mq(md) {
   padding: 20px 0;
  }
}

.c-summary th {
  width: 20%;

  @include g.mq(md) {
    width: 100px;
  }
}