@use "../../global" as g;

.p-globalNav__menu {
  display: flex;
  padding-top: 4px; //pfp
}

.p-globalNav__item {
  margin-left: 29px;
}

.p-globalNav__link {
  font-size: 1.6rem;
  font-weight: bold;
  color: g.$color-text;
  letter-spacing: 0.05em;
  line-height: 1;
  margin-right: 0;
}

.p-globalNav__link--permalink {
  color: g.$color-primary;
}

.p-spNav__link {
  font-family: "Noto Sans JP Bold";
  font-size: 1.6rem;
  color: g.$color-white;
  letter-spacing: 0.05em;
}
.p-spNav__link--permalink {
  color: #EF90A4;
}