@use "../../global" as g;

.p-company__summary {
  margin-top: 57px;
  margin-bottom: 50px;

  @include g.mq(md) {
    margin-top: 55px;
  }
}

.p-company__map {
  width: 100%;
  margin-bottom: 20px;

  @include g.mq(md) {
    margin-bottom: 15px;
  }
}
.p-company__map iframe {
  height: 400px;

  @include g.mq(md) {
    height: 300px;
  }
}

.p-company__mapLinkWarraper {
  text-align: right;
}

.p-company__maplink {
  font-family: 'Noto Sans JP';
  font-size: 1.5rem;
  color: g.$color-secondary;
  text-decoration: underline;

  @include g.mq(md) {
   font-size: 1.2rem;
  }
}

.p-companyNews {
  margin-top: 79px;
  margin-bottom: 100px;

  @include g.mq(md) {
    margin-top: 47px;
    margin-bottom: 50px;
  }
}