@use "../../global" as g;

.c-serviceList__item {
  background-color: #fff;
  padding: 50px 50px 50px;
  border-radius: 8px;
  box-shadow: 0 3px 21px rgb( 0 0 0 / 16%);

  @include g.mq(md) {
    padding: 40px 30px;
  }
}

.c-serviceList__imgWrapper {
  text-align: center;
}

.c-serviceList__img {
  font-size: 48px;
  color: g.$color-primary;

  @include g.mq(md) {
    font-size: 60px;
  }
}

.c-serviceList__ttl {
  font-size: 1.8rem;
  font-weight: bold;
  line-height: 1;
  text-align: center;
  margin-top: 5px;
  margin-bottom: 25px;
}

.c-serviceList__txt {
  font-size: 1.4rem;
  letter-spacing: 0.04em;
  line-height: 1.7;

  @include g.lineHeightSpaceFill(1.7);
}