@use "../global" as g;

.l-footer {
  font-weight: bold;
  color: g.$color-white;
  background-color: g.$color-primary;
  padding-top: 60px;
  padding-bottom: 50px; 

  @include g.mq(md) {
    font-size: 1.0rem;
    padding-top: 41px;
    padding-bottom: 29px;
  }
}

.l-footer__nav {
  margin-bottom: 22px;

  @include g.mq(md) {
    margin-bottom: 20px;
  }
}

.l-footer__copyright {
  font-size: 1.2rem;
  letter-spacing: 0.03em;

  @include g.mq(md) {
    font-size: 1.0rem;
  }
}