@use "../../global" as g;

.p-message {
  padding-top: 62px; //pfp 60px → 62px
  padding-bottom: 100px;

  @include g.mq(md) {
    padding-top: 30px;
    padding-bottom: 50px;
  }
}

.p-message__inner {
  max-width: 850px;
  width: 100%;
  margin: 0 auto;
  margin-bottom: 80px;

  @include g.mq(md) {
    margin-bottom: 50px;
  }
}

.p-message__heading {
  margin-bottom: 38px;
  text-align: center;
  
  @include g.mq(md) {
    margin-bottom: 31px;
  }
}

.p-message__ttl {
  font-size: 4rem;
  font-weight: bold;
  letter-spacing: 0.05em;
  line-height: 1.5;
  color: g.$color-primary;
  margin-bottom: 22px;

  @include g.mq(md) {
    font-size: 2.4rem;
    margin-bottom: 20px;
  }
}

.p-message__subTtl {
  font-size: 1.8rem;
  letter-spacing: 0.05em;
  line-height: 1;

  @include g.mq(md) {
    font-size: 1.5rem;
    line-height: 1.5;
  }
}

.p-messageBody {
  max-width: 795px;
  width: 100%;
  padding-top: 4px; //pfp 追加
  margin: 0 auto;
}

.p-messageBody__imgWrapper {
  width: 100px;
  height: 100px;
  float: left;
  margin-right: 25px;
  margin-bottom: 27px;

  @include g.mq(md) {
    margin-right: 15px;
    margin-bottom: 15px;
  }
}

// .p-message__img {
//   width: 100px;
//   height: 100px;
//   border-radius: 50px;
// }

.p-messageBody__txt {
  font-size: 1.6rem;
  letter-spacing: 0.05em;
  line-height: 1.6;

  @include g.mq(md) {
    font-size: 1.5rem;
    line-height: 1.5;
  }
}

.p-messageBody__txt--link {
  color: g.$color-secondary;
  text-decoration: underline;
}

.p-message__imgWrapper {
  width: 100%;
  height: 400px;

  @include g.mq(md) {
    height: 260px;
  }
}

.p-message__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: 0 75%;

  @include g.mq(md) {
    object-position: 50% 60%;
  }
}