@use "../../global" as g;

.p-news {
  margin-top: 70px;
  padding-bottom: 30px;
 
  @include g.mq(md) {
    margin-top: 32px;
    margin-bottom: 60px;
  }
}

.p-news__inner {
  display: flex;
  column-gap: 31px; //pfp

  @include g.mq(md) {
    display: block;
  }
}

.p-news__heading {
  min-width: 220px;

  @include g.mq(md) {
    margin-bottom: 22px;
  }
}
