@use "../../global" as g;

.p-hero {
  width: 100%;
  height: 600px;

  @include g.mq(md) {
    height: 460px;
  }
}

.p-hero__background {
  width: 100%;
  height: 100%;
  background-image: url(../img/pc_bg_mv.jpg);
  background-size:cover;
  background-position: center;
  background-repeat: no-repeat;
  padding-top: 201px; //pfp 170px

  @include g.mq(md) {
    background-image: url(../img/sp_mv.jpg);
  }
}

.p-hero__body {
  color: g.$color-white;
  line-height: 1;
  text-align: center;
}

.p-hero__ttl {
  font-size: 16rem;
  font-weight: bold;

  @include g.mq(md) {
    font-size: 8rem;
  }
}

.p-hero__subTtl {
  display: block;
  font-family: 'Noto Sans JP';
  font-weight: Medium;
  font-size: 2.4rem;
  letter-spacing: 0.1em;
  line-height: 1;
  padding-top: 3px;
  padding-left: 29px;
  
  @include g.mq(md) {
    font-size: 2.0rem;
    padding-left: 0;
  }
}

//hero--sub
.p-hero--sub {
  width: 100%;
  height: 190px;

  @include g.mq(md) {
    height: 160px;
  }
}

.p-hero__background--sub {
  padding-top: 76px; //pfp 67px → 76px

  @include g.mq(md) {
    background-image: url(../img/sp_mv.jpg);
    padding-top: 62px;
  }
}

.p-hero__ttl--sub {
  font-size: 4.0rem;
  font-weight: bold;
  letter-spacing: 0.1em;
  line-height: 1.175;

  @include g.mq(md) {
    font-size: 2.8rem;
  }
}
