@use "../../global" as g;

.c-hamburger {
  width: 60px;
  height: 60px;
  position: relative;
  cursor: pointer;
  background-color: g.$color-primary;
  transition: all 0.5s;
  z-index: 30;
}

.c-hamburger__item1,
.c-hamburger__item2,
.c-hamburger__item3 {
  width: 26px;
  height: 2px;
  background-color: #fff;
  border-radius: 1px;
  transition: all 0.3s ease-in-out;
}

.c-hamburger__item1 {
  content: "";
  position: absolute;
    top: 22px;
    left: 17px;
}

.c-hamburger__item2 {
  position: absolute;
    top: 30px;
    left: 17px;
}

.c-hamburger__item3 {
  content: "";
  position: absolute;
    top: 38px;
    left: 17px;
}
 
// jquery addClass .active
.l-header__hamburger.active .c-hamburger__item1 {
  top: 30px;
  transform: rotate(-45deg);
}

.l-header__hamburger.active .c-hamburger__item2 {
  display: none;
}

.l-header__hamburger.active .c-hamburger__item3 {
  top: 30px;
  transform: rotate(45deg);
}


