@use "../../global" as g;

.c-newsList__item {
  font-size: 1.5rem;
  line-height: 1.8;
  padding-top: 5px; //pfp
  padding-bottom: 18px;
  border-bottom: 1px solid #ededed;

  @include g.lineHeightSpaceFill(1.8);
}
.c-newsList__item:not(:first-child) {
  padding-top: 17px;

  @include g.mq(md) {
    padding-top: 16px;
  }
}

.c-newsList__link {
  display: flex;

  @include g.mq(md) {
    display: block;
  }
}

.c-newsList__date {
  margin-right: 40px;
}